<template>
	<view>
		<view style="background-color: #FF5725;width: 100%;height: 88px;">
			<view style="width: 100%;" :style="'height:' + system_top + 'px;'"></view>
			<view class="row" style="width: 100%;height: 40px;align-items: center;">
				<image @click="doBack()" src="../../static/back-white.png" mode="aspectFit"
					style="width: 32px;height: 32px;margin-left: 12px;"></image>
				<view style="flex: 1;">
					
				</view>
				<image src="../../static/message/shanchu.png" mode="" style="width: 20px;height: 20px;"
					:style="'margin-right:' + weixinMenuWidth + 'px;'" @click="clickRight()"></image>
			</view>
		</view>
		<image src="../../static/message/xitong.png" v-if="msgDes.type == 0" mode="aspectFit" class="xitongImage"></image>
		<image src="../../static/message/dazhe.png" v-if="msgDes.type == 1" mode="aspectFit" class="xitongImage"></image>
		<image src="../../static/message/huodong.png" v-if="msgDes.type == 2" mode="aspectFit" class="xitongImage"></image>
		<view class="tongzhi">
			{{i18n.message.tongzhi}}
		</view>
		<view class="contenV column">
			<view class="titleView">
				{{msgDes.title}}
			</view>
			<view class="timeView">
				{{msgDes.addtime}}
			</view>
			<image :src="msgDes.thumb" mode="widthFix" style="width: 100%;border-radius: 2px;height: auto;"></image>
			
			<view class="timeView">
				{{msgDes.content}}
			</view>
		</view>
	</view>
</template>

<script>
	
	export default {
		components: {
			
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		data() {
			return {
				system_top: 44,
				weixinMenuWidth: 12,
				msgID:'',
				msgDes:null
			}
		},
		onReady() {
			const res = uni.getSystemInfoSync();
		
			// #ifdef MP-WEIXIN
			let weinfo = uni.getMenuButtonBoundingClientRect();
			this.weixinMenuWidth = weinfo.width + 12
			// #endif
			if (parseInt(res.safeArea.top) > this.system_top) {
				this.system_top = parseInt(res.safeArea.top)
			}
			this.scrollHeight = res.windowHeight - 60 - this.system_top
		},
		onLoad(option) {
			this.msgID = option.id
			this.requestData()
		},
		methods: {
			doBack(){
				uni.navigateBack({
					success: res => {
						console.log(res)
					},
					fail: (error) => {
						console.log('fail', error)
						uni.reLaunch({
							url: '/pages/index/index'
						})
					}
				})
			},
			clickRight(){
				var that = this
				that.http.sendRequest('Message.Delete', {
					'id': that.msgID
				}).then(function(res) {
					if (res.code == 0) {
						that.doBack()
					}
				})
			},
			requestData(){
				var that = this
				that.http.sendRequest('Message.Read', {
					'id': that.msgID
				}).then(function(res) {
					if (res.code == 0) {
						that.msgDes = res.info[0]
					}
				})
			}
		}
	}
</script>

<style>
	page {
		width: 100%;
		height: 100%;
		background-color: #FFF;
	}
	.xitongImage {
		position: absolute;
		left: calc(50% - 32px);
		top: 66px;
		width: 64px;
		height: 64px;
		border-radius: 32px;
		background-color: #F5F6F7;
	}
	.tongzhi {
		width: 100%;
		margin-top: 50px;
		height: 20px;
		font-size: 14px;
		line-height: 20px;
		color: #181818;
		text-align: center;
	}
	.contenV {
		margin-top: 20px;
		margin-left: 15px;
		width: calc(100% - 30px);
	}
	.titleView {
		line-height: 22px;
		font-size: 16px;
		font-weight: bold;
	}
	.timeView {
		margin-top: 5px;
		line-height: 14px;
		font-size: 12px;
		color: #999999;
	}
	.timeView {
		margin-top: 10px;
		line-height: 20px;
		font-size: 14px;
		color: #383838;
	}
	
</style>
